@import url(https://fonts.googleapis.com/css2?family=Ma+Shan+Zheng&family=Mochiy+Pop+P+One&family=ZCOOL+KuaiLe&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.colorEditor {
    margin-top: 20px;
    margin-bottom: 20px;
}

.button {
    float: right;
}

.colorToChoose {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.colorButton {
    height: 50px;
    width: 100px;
    border-radius: 5px;

}

.colorButton:hover {
    cursor: pointer;
    box-shadow: 0 0 10px;
}

.wrapper {
    height: 50px;
    width: 100px;
    border-radius: 5px;
    margin-left: 20px;
    margin-right: 20px;
}

.colorButtonLight {
    box-shadow: 0 0 10px;
}

.colorPicker {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.colorBlock {
    width: 300px;
}

.colorArea {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.up {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.LeftColor {
    width: 300px;
    padding-left: 20px;
    padding-top: 20px;
}

.RightColor {
    width: 300px;
    padding-right: 20px;
    padding-top: 20px;
}

.bottomColor {
    width: 600px;
    padding-left: 20px;
    padding-bottom: 20px;
    display: flex;
    flex-direction: row;
}

.input {
    padding-top: 35px;
}

.colorHex {
    padding-top: 37px;
}

.inputFrame {
    padding: 20px;
    width: 400px;
}

.sketchPreview {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}

.sketchList {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.fontSize {
    font-size: 15px;
}

.App {
  text-align: center;
}

.one_row {
  display: flex;
  flex-direction: row;
}

.all_items {
  display: flex;
  flex-direction: column;
}

.scratch_item:hover {
  box-shadow: 0 0 10px;
  cursor: pointer;
}

.SketchList {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.Explorer {

}

.ExplorerViewer {
    margin: 0 auto 1rem;
    background-color: rgba(240 ,242,245,1);
    padding: 1rem;
    border-radius: 2rem;
}

.ExplorerOperations{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin-top: 1rem;
}
.App {
  text-align: center;
}

.two_lists {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.order_buttons {
  display: flex;
  flex-direction: row;
  padding-left: 50px;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100%;
  max-width: 900px;
}

.order_button {
  margin-right: 20px;
  text-align: center;
}

.left_list {
  display: flex;
  flex-direction: column;
  padding-left: 50px;
}

.history_list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.piece {
  display: flex;
  flex-direction: row;
  padding-bottom: 5px;
}

.pic_class {
  width: 40px;
  text-align: center;
}

.delete {
  margin-right: 5px;
  padding-right: 2px;
  width: 15px;
}

.delete:hover {
  cursor: pointer;
}

.select {
  margin-right: 5px;
  padding-right: 5px;
  width: 15px;
}

.select:hover {
  cursor: pointer;
}

.color_bind {
  display: flex;
  flex-direction: row;
  margin-right: 15px;
  width: 150px;
  justify-content: center;
}

.color_bind:hover {
  cursor: pointer;
}

.author {
  margin-right: 20px;
  width: 100px;
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow:hidden;
}

.pic_name {
  margin-right: 20px;
  width: 100px;
  text-align: center;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow:hidden;
}

.color {
  width: 100px;
  height: 20px;
}

.font {
    font: bold 25px/100% "微软雅黑", "Lucida Grande", "Lucida Sans", Helvetica, Arial, Sans;
}

.navBar {
    font-size: 20px;
    height: 50px;
    background-image: linear-gradient(to right, #F0E8E8 0%, #E8E8E8 19%, #E8E8FF 42%, #F0E8F0 69%, #E8E8FF 100%);
    position: -webkit-sticky;
    position: sticky;
    display: flex;
    flex-direction: row;
    top: 0;
    justify-content: space-between;
    z-index: 32;
}

.left {
    width: 70%;
    height: 50px;
    display: flex;
    flex-direction: row;
    padding-left: 5%;
    align-content: center;
}

.welcome {
    font-family: 'Mochiy Pop P One', sans-serif;
    font-size: 20px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.CodePlay {
    background: linear-gradient(to right, red, blue);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.right {
    width: 30%;
    height: 50px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;
}

.designer:hover {
    cursor: pointer;
}

.logOut {
    font-family: 'ZCOOL KuaiLe', cursive;
}

.logOut:hover {
    cursor: pointer;
}

.font1 {
    font-family: 'Mochiy Pop P One', sans-serif;
    font-size: 20px;
}
.scheme {
    width: 170px;
    height:300px;
    position:fixed;
    right:0;
    top:100px;
    background: rgba(200,200,200, 0.1);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    z-index: 32;
}
.scheme2 {
    width: 170px;
    height:300px;
    position:fixed;
    left:0;
    top:100px;
    background: rgba(200,200,200, 0.1);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    z-index: 32;
}

.selected {
    background-color: rgba(122, 197, 216, 0);
    padding: 5%;
    width: 100%;
    height: 50%;
}

.popularity {
    background: rgba(180, 159, 218, 0.0);
    padding: 5%;
    width: 100%;
    height: 50%;
}
